.github-card {
    background-color: rgb(255, 255, 255);
    font-size: medium;
    width: 450px;
    height: 200px;
    padding: 20px;
    margin: 10px;
    border-radius: 10px;

    -webkit-box-shadow: 10px 11px 13px -8px rgba(0, 0, 0, 0.75);
    -moz-box-shadow: 10px 11px 13px -8px rgba(0, 0, 0, 0.75);
    box-shadow: 10px 11px 13px -8px rgba(0, 0, 0, 0.75);
}

.github-card .details {
    color: rgb(56, 56, 56);
    text-decoration: none;
}

.github-card .details .name {
    display: flex;
    border-bottom-style: inset;
    border-bottom-width: 2px;
}

.github-card .details .name h3 {
    font-family: 'Varela Round', sans-serif;
    font-size: xx-large;
    text-align: left;
    margin: 0;
}

.github-card .details .name a {
    margin-top: 8px;
    margin-left: 10px;
    color: rgb(56, 56, 56);
    ;
}

.github-card .details .description-lang {
    display: flex;
    height: 140px;
}

.github-card .details .description-lang .desc {
    font-family: 'Roboto', sans-serif;
    text-align: justify;
    text-justify: inter-word;
}

.github-card .details .description-lang .lang {
    text-align: left;
    font-family: 'Roboto', sans-serif;
    list-style: none;
}

.github-card .details .description-lang .lang .lang-row {
    display: flex;
    width: max-content;
    margin-bottom: 5px;
}

.github-card .details .description-lang .lang .lang-color {
    margin-top: 5px;
    width: 10px;
    height: 10px;
    border-radius: 100%;
    margin-right: 10px;
}

.github-card .details .active-date {
    text-align: left;
    font-family: 'Quicksand', sans-serif;
}