@import url('https://fonts.googleapis.com/css2?family=Quicksand&display=swap');

.sidenav {
    height: 100%;
    width: 250px;
    position: fixed;
    top: 0;
    left: 0;
    z-index: 1;
    background-color: #282c34;
    overflow-x: hidden;
    /* Disable horizontal scroll */
    padding-top: 20px;

    /* Box Shadow  https://www.cssmatic.com/box-shadow */
    -webkit-box-shadow: 11px 0px 32px 4px rgba(0, 0, 0, 0.75);
    -moz-box-shadow: 11px 0px 32px 4px rgba(0, 0, 0, 0.75);
    box-shadow: 11px 0px 32px 4px rgba(0, 0, 0, 0.75);

}

.sidenav .profile-img {
    display: block;
    margin-left: 40px;
    margin-right: 40px;
}

/* .sidenav .profile-img a {
    padding-left: 30px;
} */

.sidenav .nav-links {
    height: 65%;
}

.sidenav .nav-links ul {
    padding: 0;
    height: 100%;
}

.sidenav .nav-links ul li {
    list-style: none;
}

.sidenav .nav-links ul li h5 {
    margin: 10px;
    margin-left: 35px;
}

.sidenav .nav-links ul li a {
    height: 100%;
    font-family: 'Quicksand', sans-serif;
    text-decoration: none;
    font-size: 25px;
    color: azure;
    display: flex;
    transition: 0.1s;
}

.sidenav .nav-links ul li a:hover {
    background-color: rgb(223, 223, 223);
    color: rgb(56, 56, 56);
    border-radius: 5px;

    -webkit-box-shadow: 3px 4px 16px -3px rgba(235, 235, 235, 1);
    -moz-box-shadow: 3px 4px 16px -3px rgba(235, 235, 235, 1);
    box-shadow: 3px 4px 16px -3px rgba(235, 235, 235, 1);
}

.contact-links {
    margin-left: 40px;
    margin-right: 40px;
}

.contact-links ul {
    justify-content: space-evenly;
    display: flex;
    list-style: none;
    padding-left: 0;
}

.contact-links a {
    color: azure;
    text-decoration: none;
}

.contact-links a :hover {
    color: rgb(138, 138, 138);
}

.fa {
    font-size: x-large;
}