@import url('https://fonts.googleapis.com/css2?family=Fira+Sans&display=swap');

.home-body {
    height: 100vh;
    display: flex;
    align-items: center;
    justify-content: center;
}

.home-body p {
    font-family: 'Fira Sans', sans-serif;
    font-size: 50px;
}

.home-body span {
    font-size: 30px;
}

.home-body span a {
    text-decoration: none;
    color: #337ab7;
}