@import url(https://fonts.googleapis.com/css2?family=Quicksand&family=Roboto&family=Varela+Round&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Fira+Sans&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Quicksand&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}
.app {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    -webkit-animation: App-logo-spin infinite 20s linear;
            animation: App-logo-spin infinite 20s linear;
  }
}

.app-body {
  margin-left: 250px;
  background-color: #e4e4e4;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: #484d53;
}

.model-container {
  display: flex;
  flex-direction: column;
}

@-webkit-keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }

  to {
    transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }

  to {
    transform: rotate(360deg);
  }
}
.home-body {
    height: 100vh;
    display: flex;
    align-items: center;
    justify-content: center;
}

.home-body p {
    font-family: 'Fira Sans', sans-serif;
    font-size: 50px;
}

.home-body span {
    font-size: 30px;
}

.home-body span a {
    text-decoration: none;
    color: #337ab7;
}
.projects-grid {
    width: 80%;
    margin-top: 30px;
    margin-bottom: 50px;
    display: grid;
    grid-template-columns: repeat(auto-fit, minmax(400px, 1fr));
}
.github-card {
    background-color: rgb(255, 255, 255);
    font-size: medium;
    width: 450px;
    height: 200px;
    padding: 20px;
    margin: 10px;
    border-radius: 10px;
    box-shadow: 10px 11px 13px -8px rgba(0, 0, 0, 0.75);
}

.github-card .details {
    color: rgb(56, 56, 56);
    text-decoration: none;
}

.github-card .details .name {
    display: flex;
    border-bottom-style: inset;
    border-bottom-width: 2px;
}

.github-card .details .name h3 {
    font-family: 'Varela Round', sans-serif;
    font-size: xx-large;
    text-align: left;
    margin: 0;
}

.github-card .details .name a {
    margin-top: 8px;
    margin-left: 10px;
    color: rgb(56, 56, 56);
    ;
}

.github-card .details .description-lang {
    display: flex;
    height: 140px;
}

.github-card .details .description-lang .desc {
    font-family: 'Roboto', sans-serif;
    text-align: justify;
    text-justify: inter-word;
}

.github-card .details .description-lang .lang {
    text-align: left;
    font-family: 'Roboto', sans-serif;
    list-style: none;
}

.github-card .details .description-lang .lang .lang-row {
    display: flex;
    width: -webkit-max-content;
    width: -moz-max-content;
    width: max-content;
    margin-bottom: 5px;
}

.github-card .details .description-lang .lang .lang-color {
    margin-top: 5px;
    width: 10px;
    height: 10px;
    border-radius: 100%;
    margin-right: 10px;
}

.github-card .details .active-date {
    text-align: left;
    font-family: 'Quicksand', sans-serif;
}
.work-experience {
    height: 100vh;
    justify-content: start;
    text-align: left;
    margin-left: 50px;
    margin-right: 50px;
}

.work-experience h4 {
    font-family: 'Varela Round', sans-serif;
    font-size: xx-large;
    font-weight: 700;
    line-height: 25px;
    margin-bottom: 0px;
}

.work-experience p {
    font-family: 'Roboto', sans-serif;
    text-align: justify;
    text-justify: inter-word;
    margin-top: 15px;
    font-size: 20px;
    line-height: 20px;
}
.sidenav {
    height: 100%;
    width: 250px;
    position: fixed;
    top: 0;
    left: 0;
    z-index: 1;
    background-color: #282c34;
    overflow-x: hidden;
    /* Disable horizontal scroll */
    padding-top: 20px;

    /* Box Shadow  https://www.cssmatic.com/box-shadow */
    box-shadow: 11px 0px 32px 4px rgba(0, 0, 0, 0.75);

}

.sidenav .profile-img {
    display: block;
    margin-left: 40px;
    margin-right: 40px;
}

/* .sidenav .profile-img a {
    padding-left: 30px;
} */

.sidenav .nav-links {
    height: 65%;
}

.sidenav .nav-links ul {
    padding: 0;
    height: 100%;
}

.sidenav .nav-links ul li {
    list-style: none;
}

.sidenav .nav-links ul li h5 {
    margin: 10px;
    margin-left: 35px;
}

.sidenav .nav-links ul li a {
    height: 100%;
    font-family: 'Quicksand', sans-serif;
    text-decoration: none;
    font-size: 25px;
    color: azure;
    display: flex;
    transition: 0.1s;
}

.sidenav .nav-links ul li a:hover {
    background-color: rgb(223, 223, 223);
    color: rgb(56, 56, 56);
    border-radius: 5px;
    box-shadow: 3px 4px 16px -3px rgba(235, 235, 235, 1);
}

.contact-links {
    margin-left: 40px;
    margin-right: 40px;
}

.contact-links ul {
    justify-content: space-evenly;
    display: flex;
    list-style: none;
    padding-left: 0;
}

.contact-links a {
    color: azure;
    text-decoration: none;
}

.contact-links a :hover {
    color: rgb(138, 138, 138);
}

.fa {
    font-size: x-large;
}
.blog-card {
    background-color: rgb(255, 255, 255);
    font-size: medium;
    width: calc(100vw - 550px);
    padding: 20px;
    margin: 10px;
    border-radius: 10px;
    box-shadow: 10px 11px 13px -8px rgba(0, 0, 0, 0.75);
}

.blog-card .title {
    display: flex;
    border-bottom-style: inset;
    border-bottom-width: 2px;
}

.blog-card .title h3 {
    font-family: 'Varela Round', sans-serif;
    font-size: xx-large;
    text-align: left;
    margin: 0;
}

.blog-card .title a {
    margin-left: 10px;
    margin-top: 8px;
    color: rgb(56, 56, 56);
}

.blog-card p {
    font-family: 'Roboto', sans-serif;
    text-align: justify;
    text-justify: inter-word;
}

.blog-card h3 {
    text-align: left;
}

.blog-card h3 a {
    font-family: 'Roboto', sans-serif;
    text-decoration: none;
    color: rgb(56, 56, 56);
}

.blog-card h3 a:hover {
    color: rgb(154, 154, 154);
}
.blog {
    height: 100vh;
    margin-top: 30px;
    margin-bottom: 50px;
}
