.blog-card {
    background-color: rgb(255, 255, 255);
    font-size: medium;
    width: calc(100vw - 550px);
    padding: 20px;
    margin: 10px;
    border-radius: 10px;

    -webkit-box-shadow: 10px 11px 13px -8px rgba(0, 0, 0, 0.75);
    -moz-box-shadow: 10px 11px 13px -8px rgba(0, 0, 0, 0.75);
    box-shadow: 10px 11px 13px -8px rgba(0, 0, 0, 0.75);
}

.blog-card .title {
    display: flex;
    border-bottom-style: inset;
    border-bottom-width: 2px;
}

.blog-card .title h3 {
    font-family: 'Varela Round', sans-serif;
    font-size: xx-large;
    text-align: left;
    margin: 0;
}

.blog-card .title a {
    margin-left: 10px;
    margin-top: 8px;
    color: rgb(56, 56, 56);
}

.blog-card p {
    font-family: 'Roboto', sans-serif;
    text-align: justify;
    text-justify: inter-word;
}

.blog-card h3 {
    text-align: left;
}

.blog-card h3 a {
    font-family: 'Roboto', sans-serif;
    text-decoration: none;
    color: rgb(56, 56, 56);
}

.blog-card h3 a:hover {
    color: rgb(154, 154, 154);
}